.App {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 655px;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}


#tsparticles {
    position: absolute; 
    width: 100%;

}


button {

    width : 250px;
    height : 70px;
    border : none;
    color : white;
    border-radius : 0.5em;
    font-size : 1.2rem;
    z-index : 2;
    cursor : pointer
  
    }

button:active{

box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.102);
width: 220px;
height: 65px;
box-sizing: border-box;
transition: ease 300ms;

}

.btnNext{

 background-color : #01fbff;

}

.btnBack{

    background-color : #989898;
   
   }
   